.checkout-container {
  width: 100%;
  display: flex;
  justify-content: center;
  height: 90vh;
}
.checkout-container .col {
  flex: 0 0 45%;
}
.checkout-container .col.col-left {
  background-color: white;
  flex: 0 0 55%;
  display: flex;
  justify-content: end;
}
.checkout-container .col.col-left .info-checkout {
  padding: 40px;
  padding-left: 0px;
  flex: 0 0 60%;
  padding-top: 80px;
}
.checkout-container .col.col-left .info-checkout h1
{
    margin-bottom: 60px;
}
.checkout-container .col.col-left .info-checkout h1,
.checkout-container .col.col-left .info-checkout h2 {
  font-weight: 700;
}
.checkout-container .col.col-left .info-checkout h1 {
  font-size: 50px;
}
.checkout-container .col.col-left .info-checkout h2 {
  font-size: 30px;
}
.checkout-container .col.col-left .billing-address {
  margin-top: 40px;
}
.checkout-container .col.col-left .payment-method {
  display: flex;
  justify-content: space-between;
}
.checkout-container .col.col-left .payment-method svg {
  margin-left: 20px;
}
.checkout-container .col.col-left .payment-method span {
  line-height: 47px;
}
.checkout-container .col.col-left .payment-method-list {
  padding-left: 0px;
  list-style-type: none;
}
.checkout-container .col.col-left .payment-method-list .payment-method-item {
  margin-top: 10px;
}
.checkout-container
  .col.col-left
  .payment-method-list
  .payment-method-item
  .form-check {
  padding: 10px;
  background-color: #f7f9fa;
  border: 1px solid #d1d7dc;
  width: 100%;
}
.checkout-container
  .col.col-left
  .payment-method-list
  .payment-method-item
  .form-check
  input {
  margin-left: 5px;
  margin-right: 20px;
}
.checkout-container
  .col.col-left
  .payment-method-list
  .payment-method-item
  .form-check
  input,
.checkout-container
  .col.col-left
  .payment-method-list
  .payment-method-item
  .form-check
  label {
  cursor: pointer;
}
.checkout-container
  .col.col-left
  .payment-method-list
  .payment-method-item
  .description-paymnent-method {
  padding: 20px;
  border: 1px solid #d1d7dc;
  border-top: none;
  margin-top: -3px;
}
.checkout-container .col-right {
  display: flex;
}
.checkout-container .col-right .summary {
  margin-left: 60px;
  margin-top: 190px;
  width: 40%;
}
.checkout-container .col-right .summary h2 {
  font-weight: 700;
}
.checkout-container .col-right .summary .summary-item
{
    display: flex;
    justify-content: space-between;
}
.checkout-container .col-right .summary .summary-total
{
    display: flex;
    justify-content: space-between;
}
.checkout-container .col-right .summary .summary-total span
{
    font-weight: 700;
}
.checkout-container .col-right .summary button
{
    margin-top: 40px;
    width: 100%;
    margin-bottom: 40px;
}
.checkout-container .col-right .summary p
{
    font-size: 14px;
}
